.field {
  & .field-input-wrapper {
    position: relative;

    & .field__field {
      width: 100%;
    }

    & .field__increase-button, .field__decrease-button {
      position: absolute;
      right: 0;
      z-index: 2;
      display: inline-flex;
      align-items: center;
      justify-content: center;
      border: 1px solid var(--white-gray);
      height: 50%;
      box-sizing: border-box;
      width: 36px;

      &.disabled {
        background-color: var(--white-gray);
        cursor: not-allowed;
      }

      &:not(.disabled):hover {
        background-color: var(--white-gray-active);
      }

      & .svg-icon {
        height: 12px;
        width: 12px;
        color: var(--slate-gray);
      }
    }

    & .field__decrease-button {
      bottom: 0;
      border-bottom-right-radius: 8px;
      border-top: 0;

      & .svg-icon {
        transform: rotate(180deg);
      }
    }

    & .field__increase-button {
      border-top-right-radius: 8px;
    }
  }
}